<template>
  <div class="menu">
    <div class="search">
      <van-search v-model="value" placeholder="请输入要搜索的商品" @input="searchGoods" />
      <div class="search-con" v-if="isSearch">
              <div
                class="search-list"
                v-for="(item, index) in searchData"
                :key="index"
              >
                <div @click="goDetail(item.pid)">{{ item.name }}</div>
              </div>
            </div>
    </div>

    <div class="tabList clearfix">
        <div class="left fl">
          <van-sidebar v-model="activeKey">
            <van-sidebar-item
              :title="item.title"
              v-for="(item, index) in productTitle"
              :key="index"
              :class="activeKey == index ? 'active' : ''"
              @click="toggleMenu(item,index, item.type)"
              >
            </van-sidebar-item>
          </van-sidebar>
        </div>

        <div class="right fl">
           <div class="item clearfix" v-for="(item,index) in productData" :key="index"  @click="goDetail(item.pid)">
               <div class="img fl">
                   <img :src="item.smallImg" alt="" class="auto-img">
               </div>
               <div class="info fl">
                   <div class="name">{{item.name}}</div>
                   <div class="enname">{{item.enname}}</div>
               </div>
               <div class="price fr">￥{{item.price}}</div>
               
               </div> 
        </div>
      </div>
    </div>

</template>

<script>
import "../assets/less/menu.less";
export default {
  name: "Menu",
  data() {
    return {
      value: "",
      activeKey:0,
      productTitle:[
          {
           title:'推荐',
          },
          {
            title:'咖啡',
            
            },
          {title:'拿铁',
          },
          {title:'瑞纳冰', }
      ],
      productData:[],
      searchData: [],
      isSearch:false
    };
  },

  created() {
      this.getTypes()
  },
  methods: {
    //获取商品类型
    getTypes() {

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "/type",
        params: {
          appkey: this.appkey
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 400) {
            let data = result.data.result;
            data.unshift({
              type: 'isHot',
              typeDesc: '推荐'
            })

            this.productTitle.map(v => {
              for (let i = 0; i < data.length; i++) {
                if (v.title == data[i].typeDesc) {
                  v.type = data[i].type;
                  break;
                }
              }
            })

            let type = this.productTitle[this.activeKey].type;

            this.getProductByType(type);

          }


        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

     //根据类型获取商品
    getProductByType(type) {

      let params = {
        appkey: this.appkey
      };
      if (type == 'isHot') {
        params.key = 'isHot';
        params.value = 1;
      } else {
        params.key = 'type';
        params.value = type;
      }


      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 500) {
            this.productData = result.data.result;
          }

        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //查看商品详情
    goDetail(pid){
      this.$router.push({name: 'Detail', params: {pid}});
    },
    //切换菜单商品
    toggleMenu(item,index, type) {
      if (this.activeKey == index) {
        return;
      }
      this.activeKey = index;
      this.getProductByType(type);


    },

     // 商品搜索
    searchGoods(value) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      }); 
      this.axios({
       
        method: "GET", 
        url: "/search", 

        params: {
          appkey: this.appkey,
          name: value,
        },
      })
        .then((res) => {
          this.$toast.clear();
          if (res.data.code == "Q001") {
            this.isSearch = true;
            this.searchData = res.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

  },
};
</script>

<style lang="less" scoped>
</style>